<template>
  <v-card>
    <!--    -->
    <v-card-text>

      <div style="color:#7895B2; font-size: 2.1em;" class="py-6">
          
      </div>

      <div style="color:#7895B2; font-size: 1.3em;">
          
      </div>

      <div class="searchbox mb-6">
        <div class="header">
          <v-text-field
            placeholder="  "
            hide-details="auto"
            id="inputText"
            @keyup="ingredient_filter"
            class="mx-6"
            v-model="name"
          ></v-text-field>
        </div>
  
        <div class="ingredient-container mx-6">
          <div v-for="i in ingredients" :key="i.name">
            <span ref="ingreName" class="ingreName visible" @click="clickIngredient">{{i.name}}</span>
          </div>
        </div>
      </div>
      
      <div style="color:#7895B2; font-size: 1.3em;">
        
      </div>

      <v-text-field
        placeholder=" "
        hide-details="auto"
        id="inputText"
        class="mx-6 mb-8"
        v-model="amount"
      ></v-text-field>

      <div style="color:#7895B2; font-size: 1.3em;">
        
      </div>

      <v-text-field
        placeholder=" "
        id="inputUnit"
        @click="units_filter"
        class="mx-6 mb-0"
      ></v-text-field>

      <div class="units-container mx-6">
        <div v-for="i in units" :key="i.name">
          <span ref="unitsName" class="unitsName visible" @click="clickUnit">{{i.name}}</span>
        </div>
        <div class="mb-13"></div>
      </div>

      <div style="color:#7895B2; font-size: 1.3em;">
        
      </div>

      <v-date-picker 
        v-model="expiry_date" 
        :show-current="true" 
        :landscape="true" 
        :reactive="true"
        color="#7895B2"
        class=""
      >
      </v-date-picker>      

      <div class="d-flex justify-center">
         <v-checkbox label="  " v-model="is_expiry_not_exist"></v-checkbox>
      </div>
     
      
    </v-card-text>

    <!--   -->
    <v-card-actions class="justify-center">
      <v-btn
        color="#7895B2"
        x-large
        @click="addIngre()"
      >
        
      </v-btn>
    </v-card-actions>

    <v-snackbar v-model="snackbar" timeout="3000">
      {{ snackbarContents }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>

  </v-card>
</template>

<style>

.searchbox{
  overflow: hidden;
}

.ingredient-container{
  max-height:100px;
  overflow: scroll;
}

.units-container{
  overflow: auto;
}

.ingreName{
  display: none;
  cursor: pointer;
}

.unitsName{
  display: none;
  cursor: pointer;
}

.visible{
  display: flex;
}

