export default{
  name: "addIngredient",
  data() {
    return {
      snackbar: false,
      snackbarContents: "",

      name: "",
      amount: "",
      unit: "",
      expiry_date: null,
      nickname: "",

      //  
      ingredients : [
        {name: ""},
