<template>
  <v-container>
    <v-row justify="center">
      <v-col class="col-xl-8 col-md-10">
        <!--    -->
        <v-card min-height="1000" color="#f5efe6">
          <!--    -->
          <v-btn text to="/recipe" class="ml-5 mt-5"> -  </v-btn>
          <!--    -->
          <v-form ref="form" v-model="valid" lazy-validation class="mt-5">
            <div class="line mx-5"></div>

            <div class="px-10 d-flex align-center my-text">
              <span class="mr-16"> </span>
              <v-divider vertical />
              <v-text-field
                name="name"
                label=" "
                id="id"
                class="ml-16"
                v-model="recipeTitle"
                :rules="title_rule"
              ></v-text-field>
            </div>
            
            <div class="line mx-5"></div>

            <div class="px-10 d-flex align-center my-text">
              <span class="mr-16"> </span>
              <v-divider vertical></v-divider>
              <v-btn @click="showCategoryDialog" class="ml-14"> </v-btn>
              <span class="ml-16"> : {{recipeCategory}}</span>
            </div>

            <div class="line mx-5"></div>

            <div class="px-10 d-flex align-center my-text">
              <span class="mr-16"> </span>
              <v-divider vertical></v-divider>
              <dropdown class="my-dropdown-toggle ml-15"
                :options="hotLevel" 
                :selected="hotLevelObject" 
                v-on:updateOption="methodToRunOnSelect_spicy" 
                :placeholder="' '"
                :closeOnOutsideClick="true"
              />
            </div>

            <div class="line mx-5"></div>

            <div class="px-10 d-flex wrap align-center ingredients" style="position:relative">
              <span class="ml-1 mr-14 my-text">  </span>
              <v-divider vertical class="mr-11" />
              <div class=ma-3>
                <!--   v-for -->
			          <div v-for="(item,index) in ingredient" :key="item.name">
			          	<span style="vertical-align: text-top" class="my-text">{{ item.name }} {{ item.amount }}{{ item.unit }}</span>
                  <v-btn @click="beforeEdit(item)" small text color="success pa-5">
                    <v-icon>mdi-pencil-outline</v-icon>
                  </v-btn>
                  <v-btn @click="ingredient.splice(index,1)" small text color="warning pa-5">
                    <v-icon>mdi-close-box</v-icon>
                  </v-btn>
                  <br/>
                  
			          </div>
              </div>
              
              <div class="d-flex">
                <v-btn @click="showAddIngredientDialog" color="success pa-5" class="add-ingredient">
                  <v-icon>mdi-plus-circle-outline</v-icon>
                  <span> </span>
                </v-btn>
              </div>
              
            </div>
            
            <div class="line mx-5"></div>

            <!--    -->
            <v-textarea
              outlined
              class="mt-5 mx-5"
              name="name"
              label="  "
              placeholder="  "
              height="300"
              background-color="white"
              v-model="recipeDescription"
              :rules="description_rule"
            ></v-textarea>
            
          </v-form>

          <div class="d-flex justify-end mr-5 pb-5">
            <v-btn color="#AEBDCA" class="mr-5" @click="$router.go(-1)"></v-btn>
            <v-btn color="#AEBDCA" class="mr-2" @click="editRecipe()"></v-btn>
          </div>
        </v-card>
      </v-col>
    </v-row>

    <!--    -->
    <v-dialog
      max-width="300"
      v-model="categoryDialog"
    >
      <category-dialog
        @category="selectCategory"
        @hide="hideCategoryDialog"
      />
    </v-dialog>

    <!--    -->
    <v-dialog
      max-width="500"
      v-model="addIngredientDialog"
    >
      <add-ingredient-dialog
        :isRecipe='true'
        @add="add"
        @update="update"
        @hide="hideAddIngredientDialog"
      />
    </v-dialog>

    <!--    -->
    <v-dialog
      max-width="500"
      v-model="editIngredientDialog"
    >
      <edit-ingredient-dialog
        :isRecipe="true"
        :idP="id"
        :nameP="name"
        :amountP="amount"
        :unitP="unit"
        @edit="edit"
        @update="update"
        @hide="hideEditIngredientDialog"
      />
    </v-dialog>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle="headerTitle"
        btn1Title=""
        :btn2="false"
        @hide="hideDialog"
      >
        <template v-slot:body>
          <!--    -->
          <div>{{content1}}<br/>{{content2}}</div>
        </template>
      </popup-dialog>
    </v-dialog>

    <v-snackbar v-model="snackbar" timeout="3000">
      {{ snackbarContents }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>

  </v-container>
</template>

<style scoped>
.line{
  border:1px solid #AEBDCA
}

.ingredients{
  min-height: 300px;
}

.add-ingredient{
  position: absolute;
  bottom: 5%;
  right: 5%;
}

.my-text{
  color: #42688e;
}
.temp {
  max-height: 100px;
  overflow: scroll;
}

</style>

<script>
import herokuAPI from '@/api/heroku.js';
import dropdown from 'vue-dropdowns';
import router from '@/router/index.js';
import AddIngredientDialog from '@/components/addIngredient.vue';
import EditIngredientDialog from '@/components/editIngredient.vue';
import PopupDialog from '@/components/popup.vue';
import CategoryDialog from '@/components/Category.vue'

export default{
  components: {
    'dropdown': dropdown,
    PopupDialog,
    AddIngredientDialog,
    EditIngredientDialog,
    CategoryDialog
  },
  data () {
    return {
      valid: true,

      addIngredientDialog: false,
      popupDialog: false,
      categoryDialog: false,
      editIngredientDialog: false,
      editIngreID: -1,

      headerTitle: "",
      content1: "",
      content2: "",
      btn1Title: "",

      id: -1,
      name: "",
      amount: -1,
      unit: "",

      snackbar: false,
      snackbarContents: "",

      userNN: "",

      requestRecipe: [],
      ingredient: [],
      hotLevel: [
        { name: '0'},
        { name: '1'},
        { name: '2'},
        { name: '3'},
        { name: '4'},
        { name: '5'}
      ],
      hotLevelObject: {
        name: ' ',
      },
      title_rule: [
        v => !!v || ' .',
      ],
      recipeTitle: "",
      recipeCategory: null,
      recipeSpicy: 0,
      recipeDescription: "",
      description_rule: [
        v => !!v || ' .',
      ],
    }
  },
  mounted() {
    let pid = this.$route.params.id;
    console.log("post_id", pid);
    if(pid == null) {
      console.log("ERROR");
    }
    const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
    let vm = this;
    vm.userNN = UserInfo.nickname;
    
  //   
    herokuAPI.recipeLookup(pid, vm.userNN)
      .then(function(response) {
        console.log("  ", response);
        if(response.status == 200) {
            console.log(" ");
            vm.requestRecipe = response.data.recipeInfo;
            vm.recipeTitle = vm.requestRecipe.title;
            vm.recipeCategory = vm.requestRecipe.category;
            vm.recipeSpicy = vm.requestRecipe.degree_of_spicy;
            vm.recipeDescription = vm.requestRecipe.description;
            vm.ingredient = vm.requestRecipe.Recipe_Ingredients;
          }
      })
      .catch(function (e) {
        if(e.response.status == 500) {
          console.log("500 DB ");
          vm.requestFailPopup();
        } else if(e.response.status == 502) {
          console.log("502 Unknown error");
          vm.requestFailPopup();
        }
      });
  },
  methods: {
  //  
    showDialog() { //  
      this.popupDialog = true;
    },
    hideDialog() { //  
      this.popupDialog = false;
    },
    requestFailPopup() {
      this.headerTitle = "  ";
      this.content1 = "  ";
      this.content2 = ".";
      this.showDialog();
    },
    showCategoryDialog() {
      this.categoryDialog = true;
    },
    hideCategoryDialog() {
      this.categoryDialog = false;
    },
    selectCategory(name) {
      this.recipeCategory = name;
    },
  //   
    showAddIngredientDialog() {
      this.addIngredientDialog = true;
    },
    hideAddIngredientDialog() {
      this.addIngredientDialog = false;
    },
    add(ingre) {
      let vm = this;
      const addIngre = {
        "id": null,
        "name": ingre.name,
        "post_id": null,
        "unit": ingre.unit,
        "amount": ingre.amount
      }
      var index = this.ingredient.findIndex(e => e.name === ingre.name);
      if(index != -1) {
        vm.snackbarContents = "  ."
        vm.snackbar = true;
        return;
      }
      this.ingredient.push(addIngre);
    },
  //   
    showEditIngredientDialog() {
      this.editIngredientDialog = true;
    },
    hideEditIngredientDialog() {
      this.editIngredientDialog = false;
    },
    beforeEdit(object) {
      this.editIngreID = object.id;
      this.id = object.id;
      this.name = object.name;
      this.amount = object.amount;
      this.unit = object.unit;
      this.showEditIngredientDialog();
    },
    edit(ingre) {
      var index = this.ingredient.findIndex(e => e.id === this.editIngreID);
      this.ingredient[index].amount = ingre.amount;
      this.ingredient[index].name = ingre.name;
      this.ingredient[index].unit = ingre.unit;
    },
    update() {
      this.$router.go();
    },
    
    editRecipe() {
      let vm = this;
      const validate = this.$refs.form.validate();
      if(!validate) {
        vm.snackbarContents = "   (  )";
        vm.snackbar = true;
        return;
      }
      if(vm.recipeCategory == null) {
        vm.snackbarContents = "   ()";
        vm.snackbar = true;
        return;
      }
      if(vm.ingredient.length == 0) {
        vm.snackbarContents = "   ()";
        vm.snackbar = true;
        return;
      }
      const recipe = JSON.stringify ({
        "post_id": vm.requestRecipe.post_id,
        "nickname": vm.userNN,
        "title": this.recipeTitle,
        "category": this.recipeCategory,
        "degree_of_spicy": this.recipeSpicy,
        "description": this.recipeDescription,
        "views": 0,
        "like_count": 0,
        "comment_count": 0,
        "upload_time": "2022-11-21T15:10:03.102840+09:00",
        "Recipe_Ingredients": vm.ingredient,
        "comments": []
      }
      );
      console.log(recipe);
      herokuAPI.recipeEdit(recipe) 
        .then(function (response) {
          console.log(" ",  recipe);
          if(response.status == 200) {
            console.log(" ", response.data);
            router.go(-1);
          }
        })
        .catch(function (e) {
          if(e.response.status == 500) {
            console.log("500 DB error");
            vm.headerTitle = "  ";
            vm.content1 = "  ."
            vm.showDialog();
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.headerTitle = "  ";
            vm.content1 = "  ."
            vm.showDialog();
          }
        });
    },
    methodToRunOnSelect_category(payload) {
      this.object = payload;
      this.recipeCategory = this.object.name
    },
    methodToRunOnSelect_spicy(payload) {
      this.object = payload;
      if(this.object.name == "0") this.recipeSpicy = 0;
      else if(this.object.name == "1") this.recipeSpicy = 1;
      else if(this.object.name == "2") this.recipeSpicy = 2;
      else if(this.object.name == "3") this.recipeSpicy = 3;
      else if(this.object.name == "4") this.recipeSpicy = 4;
      else if(this.object.name == "5") this.recipeSpicy = 5;
    }
  }
}
</script>