<template>
  <v-container>
    <v-row justify="center">
      <v-col class="col-xl-8 col-md-10">
        <!--      -->
        <v-card min-height="100" color="#f5efe6" class="mb-3">
          <!--    -->
          <div class="find-cook flex align-end mb-2">
            <dropdown class="my-dropdown-toggle my-0 ml-5"
              :options="search_standard"
              :selected="search_object" 
              v-on:updateOption="searchStandardRunOnSelect" 
              :placeholder="' '"
              :closeOnOutsideClick="true">
            </dropdown>
            <v-text-field
              label=" "
              v-model="searchText"
              hide-details="auto"
              class="mx-5"
            ></v-text-field>
            <v-btn @click="beforeSelectKeyword" class="mx-3" color="#E8DFCA">
              
            </v-btn>
          </div>
          <div class="black-line mx-3"></div>
          <!--    -->
          <div class="category-search-dropdown mt-2 flex-column">
            <v-btn color="#f5efe6" depressed @click="showCategoryDialog"> </v-btn>
          </div>
        </v-card>

        <v-card min-height="calc(100vh - 233px)" color="#f5efe6">
          <!--       -->
          <div class="recipe-top d-flex justify-space-between align-center pa-5">
            <span style="color:#7895B2; font-weight:900; font-size:1.3em;"> </span>
            <!--    -->
            <dropdown class="my-dropdown-toggle"
              :options="sort_standard" 
              :selected="sort_object" 
              v-on:updateOption="sortStandardRunOnSelect" 
              :placeholder="' '"
              :closeOnOutsideClick="true">
            </dropdown>
          </div>

          <!--   -->
          <v-btn fab to="/recipe/create" x-large color="primary" class="write-icon">
            <v-icon dark>mdi-pencil-outline</v-icon>
          </v-btn>

          <v-row v-if="!isSearchExist" justify="center">
            <v-col cols="12">
              <p style="text-align:center; font-size:1.2em;" class="mt-10">
                  .
              </p>
            </v-col>
          </v-row>

          <div v-if="isSearchExist">
            <!--  v card -->
            <v-card height="100" class="mx-5 mb-5" v-for="item in recipes" :key="item.post_id" @click="toLookup(item.post_id)">
              <div class="d-flex align-center">
                <!--    . -->
                <span class="mx-10 py-3" style="font-size:1.1em; font-weight:600; color:#7895B2">{{item.title}}</span>
                <v-icon color="red" v-if="item.degree_of_spicy>=1">mdi-chili-mild</v-icon>
                <v-icon color="red" v-if="item.degree_of_spicy>=2">mdi-chili-mild</v-icon>
                <v-icon color="red" v-if="item.degree_of_spicy>=3">mdi-chili-mild</v-icon>
                <v-icon color="red" v-if="item.degree_of_spicy>=4">mdi-chili-mild</v-icon>
                <v-icon color="red" v-if="item.degree_of_spicy>=5">mdi-chili-mild</v-icon>
              </div>
              <div style="border: 0.5px solid #7895B2;" class="mx-5"></div>
              <div class="d-flex align-center justify-space-between">
                <div style="color:#7895B2" class="ml-10 py-3">
                  <!--    . -->
                  <span class="mr-3">{{item.show_upload_time}}</span>
                  <!--     -->
                  <span>{{item.nickname}}</span>
                </div>
                <div class="mr-6">
                  <!--    . -->
                  <v-icon color="red">mdi-thumb-up-outline</v-icon> {{item.like_count}}
                  <!--    . -->
                  <v-icon color="blue" class="ml-2">mdi-comment-processing-outline</v-icon> {{item.comment_count}}
                  <!--    . -->
                  <v-icon color="green" class="ml-2">mdi-eye-outline</v-icon> {{item.views}}
                </div>
              </div>
            </v-card>
          </div>
          

          <!--   -->
          <v-pagination 
            v-model="page" 
            :length="pageLength"
            class="pb-10"
            @input="handlePage"  
          >
          </v-pagination>
        </v-card>
      </v-col>
    </v-row>

    <!--    -->
    <v-dialog
      max-width="300"
      v-model="categoryDialog"
    >
      <category-dialog
        @category="beforeSelectCategory"
        @hide="hideCategoryDialog"
      />
    </v-dialog>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle=headerTitle
        :btn1Title=btn1Title
        :btn2=btn2
        @hide="hideDialog"
      >
        <template v-slot:body>
          <!--    -->
          <div>{{ content1 }}</div>
        </template>
      </popup-dialog>
    </v-dialog>

    <v-snackbar v-model="snackbar" timeout="3000">
      {{ snackbarContents }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>

  </v-container>
</template>

<style scoped>
.find-cook{
  display: flex;
}
.black-line{
  border: 0.4px solid #7895B2;
}
.category-search-dropdown{
  display: flex;
  justify-content: center;
}
.write-icon{
  position: fixed;
  bottom: 10%;
  right: 5%;
  z-index: 9;
}
.my-text{
  color:#7895B2;
}
</style>

<script>
import herokuAPI from '@/api/heroku.js';
import router from '@/router/index.js';
import dropdown from 'vue-dropdowns';
import PopupDialog from '@/components/popup.vue';
import CategoryDialog from '@/components/Category.vue'

export default{
  components: {
    PopupDialog,
    'dropdown': dropdown,
    CategoryDialog
  },
  data () {
    return {
    //
      popupDialog: false,
      headerTitle: "",
      content1: "",
      btn1Title: "",
      btn2: false,
      categoryDialog: false,

      snackbar: false,
      snackbarContents: "",

    // 
      recipes: [],
      recipeID: null,
      total_page: null,

    //
      // 
      search_standard: [
        {name: ' '},
        {name: ''}
      ],
      search_object: {
        name: ' ',
      },
      //  objects
      sort_standard: [
        { name: ' '},
        { name: ' '},
        { name: ' '}
      ],
      sort_object: {
        name: ' ',
      },

      //  
      currentSearchStandard : " ",
      searchText: "",
      searchTextStorage: "",
      searchTypeStorage: "",
      category: "",

      currentRequestType : "",

      // 
      pageLength : 1,

      // 
      page : 1,

      //  
      isSearchExist : true,

    }
  },
  mounted() {
    let vm = this;
    herokuAPI.recipeList(1)
      .then(function(response) {
        console.log("  ", response);
        if(response.status == 200) {
            console.log(" ");


            vm.pageLength = response.data.total_page;
            for(let i = 0; response.data.recipeList[i] != null; i++) {
              // 
              var parsedTime = response.data.recipeList[i].upload_time.split(/[T]/);
              response.data.recipeList[i].show_upload_time = parsedTime[0];

              vm.recipes.push(response.data.recipeList[i]);
            }
          }
      })
      .catch(function (e) {
        if(e.response.status == 500) {
          console.log("500 DB ");
          vm.requestFailPopup();
        } else if(e.response.status == 502) {
          console.log("502 Unknown error");
          vm.requestFailPopup();
        }
      });
  },
  methods: {
  //  
    showDialog() { //  
      this.popupDialog = true;
    },
    hideDialog() { //  
      this.popupDialog = false;
    },
    requestFailPopup() { // 
      this.headerTitle = " ";
      this.content1 = "   .";
      this.showDialog();
    },
    searchRequestFailPopup() { //  
      this.headerTitle = " ";
      this.content1 = "   .";
      this.showDialog();
    },
    sortRequestFailPopup() { //  
      this.headerTitle = " ";
      this.content1 = "    .";
      this.showDialog();
    },

    showCategoryDialog() {
      this.categoryDialog = true;
    },
    hideCategoryDialog() {
      this.categoryDialog = false;
    },
    beforeSelectCategory(name) {
      let vm = this;
      vm.currentRequestType = "";
      vm.category = name;
      vm.selectCategory(name, 1);
    },
    selectCategory(name, page) {
      let vm = this;
      const searchInfo = JSON.stringify({
        "searchType": "",
        "categories": name,
        "keywordType": null,
        "keyword": null,
        "page": page
      });
      vm.recipes = [];
      herokuAPI.recipeSearch(searchInfo)
        .then(function(response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log(" ");

            for(let i = 0; response.data.recipeList[i] != null; i++) {
              vm.recipes.push(response.data.recipeList[i]);
            }

            vm.pageLength = response.data.total_page;

            if(response.data.recipeList.length == 0){
              vm.isSearchExist = false;
            }
          }
        })
        .catch(function (e) {
          if(e.response.status == 500) {
            console.log("500 DB ");
            vm.searchRequestFailPopup();
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.searchRequestFailPopup();
          }
        });
    },
    beforeSelectKeyword() {
      if(this.searchText == "") {
        this.snackbarContents = " ."
        this.snackbar = true;
      }
      this.searchTextStorage = this.searchText;
      this.searchTypeStorage = this.search_object.name;
      this.selectKeyword(1);
    },
    selectKeyword(page) {
      let vm = this;
      vm.currentRequestType = "";
      const searchInfo = JSON.stringify({
        "searchType": "",
        "categories": null,
        "keywordType": vm.searchTypeStorage,
        "keyword": vm.searchTextStorage,
        "page": page
      });
      vm.recipes = [];
      console.log(searchInfo);
      herokuAPI.recipeSearch(searchInfo)
        .then(function(response) {
          console.log(" ", response);
          if(response.status == 200) {
              console.log(" ");
              for(let i = 0; response.data.recipeList[i] != null; i++) {
                vm.recipes.push(response.data.recipeList[i]);
              }

              if(response.data.recipeList.length == 0){
                vm.isSearchExist = false;
              }

              vm.pageLength = response.data.total_page;
            }
        })
        .catch(function (e) {
          if(e.response.status == 500) {
            console.log("500 DB ");
            vm.searchRequestFailPopup();
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.searchRequestFailPopup();
          }
        });
    },

  //      
    toLookup(recipeID) {
      router.push({
        path: "/recipe/lookup/"+recipeID,
      })
    },

    sortRecipeList(page, order_by) {
      let vm = this;
      vm.recipes = [];
      vm.currentRequestType = ""
      
      const sortInfo = JSON.stringify({
        "arrangeBy": order_by,
        "page": page
      });
      herokuAPI.recipeSort(sortInfo)
        .then(function (response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log(" ");
            for(let i = 0; response.data.recipeList[i] != null; i++) {
              vm.recipes.push(response.data.recipeList[i]);
            }
            window.scrollTo({top:0});
          }
        })
        .catch(function (e) {
          if(e.response.status == 500) {
            console.log("500 DB ");
            vm.sortRequestFailPopup();
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.sortRequestFailPopup();
          }
        });
    },

    //     
    searchStandardRunOnSelect(payload) {
      this.object = payload;
      if (this.object.name == " ") {
        console.log("  ");
        this.search_object.name = ' ';
        this.currentSearchStandard = ' ';
      }else if(this.object.name == "") {
        console.log(" ");
        this.search_object.name = '';
        this.currentSearchStandard = '';
      }
    },

    //     
    sortStandardRunOnSelect(payload) {
      this.object = payload;
      if (this.object.name == " ") {
        console.log("  ");
        this.sort_object.name = " ";

        this.page = 1;
        console.log(this.page, this.sort_object.name);
        this.sortRecipeList(this.page, this.sort_object.name);

      } else if (this.object.name == " ") {
        console.log("  ");
        this.sort_object.name = " ";

        this.page = 1;
        console.log(this.page, this.sort_object.name);
        this.sortRecipeList(this.page, this.sort_object.name);

      } else if (this.object.name == " ") {
        console.log("  ");
        this.sort_object.name = " ";

        this.page = 1;
        console.log(this.page, this.sort_object.name);
        this.sortRecipeList(this.page, this.sort_object.name);

      }
    },

    // 
    handlePage(page){
      let vm = this;
      if(vm.currentRequestType == "") vm.sortRecipeList(page, vm.sort_object.name);
      else if (vm.currentRequestType == "") vm.selectCategory(vm.category, page);
      else if (vm.currentRequestType == "") vm.selectKeyword(page);
    }
  }
}
</script>