<template>
  <v-card class="overflow-y-auto">
    <!--   -->
    <v-card-title class="justify-start">
       
    </v-card-title>
    
    <v-card-text>
      <v-form ref="form" v-model="valid" lazy-validation>
        <span> </span>
        <div>
          <v-text-field
            v-if="receiverFixed"
            v-model="mailReceiver"
            disabled
            outlined
          ></v-text-field>
          <v-text-field
            v-else
            label="   "
            v-model="mailReceiver"
            :rules="receiver_rule"
            outlined
          ></v-text-field>
        </div>
      
        <span></span>
        <div>
          <v-text-field
            label="  "
            v-model="mailTitle"
            :rules="title_rule"
            outlined
          ></v-text-field>
        </div>

        <span></span>
        <div>
          <v-textarea
            label="  "
            v-model="mailContents"
            :rules="contents_rule"
            outlined
          ></v-textarea>
        </div>
      </v-form>
    </v-card-text>

    <!--   -->
    <v-card-actions class="justify-center mr-2 pb-4">
      <v-btn 
        color="#7895B2"
        small
        @click="$emit('hide')"
      >
        
      </v-btn>
      <v-btn
        color="#7895B2"
        small
        @click="sendMail"
      >
        
      </v-btn>
    </v-card-actions>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        headerTitle=" "
        btn1Title=""
        :btn2=false
        @hide="hideDialog"
      >
        <template v-slot:body>
          <!--    -->
          <div>   . </div>
        </template>
      </popup-dialog>
    </v-dialog>

    <v-snackbar v-model="snackbar" timeout="3000">
      {{ snackbarContents }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>

  </v-card>
</template>

<script>
import PopupDialog from '@/components/popup.vue';
import herokuAPI from '@/api/heroku.js';

export default{
  name: "CreateMail",
  components: {
    PopupDialog
  },
  data() {
    return {
      valid: true,

      popupDialog: false,
      mailReceiver: "",
      mailTitle: "",
      mailContents: "",

      snackbar: false,
      snackbarContents: "",

    //  
      receiver_rule: [
        v => !!v || '   .',
        v => !(v && v.length < 6) || '  6 .',
      ],
      title_rule: [
        v => !!v || '  .',
      ],
      contents_rule: [
        v => !!v || '  .',
      ],
    };
  },
  props: {
    receiverFixed: {
      type: Boolean,
      default: false,
    },
    receiver: {
      type: String,
      default: "",
    },
  },
  mounted(){
    this.mailReceiver = this.receiver;
  },
  methods: {
    showDialog(){ //  
      this.popupDialog = true
    },
    hideDialog(){ //  
      this.popupDialog = false
    },
    emitMethod(){
      if(this.receiverFixed == false) this.$emit('update');
      this.$emit('hide');
    },
    sendMail(){
      let vm = this;
      const validate = this.$refs.form.validate();
      if(!validate) {
        vm.snackbarContents = "   ."
        vm.snackbar = true;
        return;
      }
      console.log("  ");
      const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
      const sendMail = JSON.stringify (
        {
          "mail_id" : null,
          "nickname" : UserInfo.nickname,
          "receiver" : vm.mailReceiver,
          "title" : vm.mailTitle,
          "contents" : vm.mailContents,
          "send_time" : "",
          "sender_check" : 0,
          "receiver_check" : 0
        }
      );
      herokuAPI.mailSend(sendMail) 
        .then(function (response) {
          console.log("response", response);
          if(response.status == 200) {
            console.log(" ", response.data);
            vm.emitMethod();
          }
        })
        .catch(function (e) {
          if(e.response.status == 404) {
            console.log("404 error");
            vm.showDialog();
          } else if(e.response.status == 500) {
            console.log("500 Unknown error");
            vm.showDialog();
          }
        });
    },
  }
};
</script>