<template>
  <v-container>
  
    <v-row justify="center">
      <v-col class="col-xl-8 col-md-10">
        <v-card height="md-800 xl-1000" color="#f5efe6">
          <v-btn text @click="$router.go(-1)" class="ml-5 mt-5"> - </v-btn>
          <!--  ,  -->
          
            <v-row>
              <v-col>
                <v-card fill-height color="#f5efe6" class="mt-10" flat>
                  <v-row>
                    <v-col cols="3" class="py-0 my-0">
                      <v-card fill-height color="#f5efe6" flat>
                        <v-card-title primary-title style="color:#7895B2">
                          
                        </v-card-title>
                      </v-card>
                    </v-col>
                    <v-col class="py-0 my-0">
                      <v-card fill-height color="#f5efe6" flat>
                        <!--   -->
                        <v-card-title primary-title >
                          {{ requestPhoto.nickname }}
                        </v-card-title>
                      </v-card>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col cols="3" class="py-0 my-0">
                      <v-card fill-height color="#f5efe6" flat style="color:#7895B2">
                        <v-card-title>
                          
                        </v-card-title>
                        
                      </v-card>
                    </v-col>
                    <v-col class="py-0 my-0">
                      <v-card fill-height color="#f5efe6" flat>
                        <!--   -->
                        <v-card-title primary-title>
                          {{ requestPhoto.upload_time.split(/[T]/)[0] }}
                        </v-card-title>
                      </v-card>
                    </v-col>
                  </v-row>
                </v-card>
              </v-col>
            </v-row>
            
            <!--   -->
            <v-row>
              <v-col offset="2" cols="8">
                <v-img
                    max-height="250"
                    contain
                    :src="'data:image/jpeg;base64,'+requestPhoto.photo_link"
                  ></v-img>
              </v-col>
          
              <v-col cols="2" class="d-flex align-end">
                <v-row justify="start">
                  <div class=".buttons">
                    <!--     -->
                    <v-btn v-if="isMine" @click="deletePopup" icon x-large>  <!--@click="deletePhoto"-->
                      <v-icon x-large>mdi-delete-outline</v-icon>
                      <div></div>
                    </v-btn>
                    
                    <v-card height="20" color="#f5efe6" flat></v-card>
                    <!--     -->
                    <v-btn v-if="!isMine" @click="showMailCreate" icon x-large>
                      <v-icon x-large>mdi-email-arrow-right-outline</v-icon>
                      <div></div>
                    </v-btn>
                    
                    <v-card height="20" color="#f5efe6" flat></v-card>
                    <!--     -->
                    <v-btn v-if="!isMine" @click="showReportDialog" icon x-large>
                      <v-icon x-large>mdi-alert-octagon</v-icon>
                      <div></div>
                    </v-btn>
                  </div>
                    
                </v-row>
                
              </v-col>
            </v-row>

            <v-row justify="center">
              <v-card color="#f5efe6" height="50" flat >
                <!--     -->
                <v-btn @click="likePhoto" icon x-large>
                  <v-icon x-large>mdi-thumb-up-outline</v-icon>
                  <div v-if="!isLikedAfter"></div>
                  <div v-if="isLikedAfter"> </div>
                </v-btn>
              </v-card>
            </v-row>

            <!--     -->
            <v-row justify="center" class="mt-10 thumbs">
                {{ requestPhoto.like_count }}
            </v-row>
        </v-card>
      </v-col>
    </v-row>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle=headerTitle
        :btn1Title=btn1Title
        :btn2Title=btn2Title
        :btn2=btn2
        @hide="hideDialog"
        @submit="checkDialog"
      >
        <template v-slot:body>
          <!--    -->
          <div>{{ content1 }}<br>{{ content2 }}</div>
        </template>
      </popup-dialog>
    </v-dialog>

    <!--    -->
    <v-dialog
      max-width="500"
      v-model="createMailDialog"
    >
      <create-mail-dialog
        :receiverFixed="true"
        :receiver=requestPhoto.nickname
        @hide="hideMailCreate"
      />
    </v-dialog>

    <!--    -->
    <v-dialog
      max-width="400"
      v-model="reportDialog"
    >
      <report-dialog
        :postType="2"
        :postID=requestPhoto.post_id
        @hide="hideReportDialog"
      />
    </v-dialog>

  </v-container>
</template>

<script>
import herokuAPI from '@/api/heroku.js';
import router from '@/router/index.js';
import PopupDialog from '@/components/popup.vue';
import ReportDialog from '@/components/report.vue';
import CreateMailDialog from '@/components/createMail.vue';

export default{
  components: {
    PopupDialog,
    ReportDialog,
    CreateMailDialog
  },
  data(){
    return{
    // 
      popupDialog: false,
      reportDialog: false,
      createMailDialog: false,
      headerTitle: "",
      content1: "",
      content2: "",
      btn1Title: "",
      btn2Title: "",
      btn2: false,

      requestPhoto: [],
      isLikedBefore: null,
      isLikedAfter: null,
      isMine: null,
      deletePost: false,
    }
  },
  mounted() {
    let pid = this.$route.params.id;
    console.log("post_id", pid);
    if(pid == null) {
      console.log("ERROR");
    }
    const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
    let vm = this;
    herokuAPI.photoLookup(pid, UserInfo.nickname)
      .then(function(response) {
        console.log(" ", response);
        if(response.status == 200) {
            console.log(" ");
            vm.requestPhoto = response.data.photoInfo;
            vm.isLikedBefore = response.data.likeInfo;
            vm.isLikedAfter = vm.isLikedBefore;
            if(vm.requestPhoto.nickname == UserInfo.nickname) {
              vm.isMine = true;
            } else {
              vm.isMine = false;
            }
          }
      })
      .catch(function (e) {
        if(e.response.status == 500) {
          console.log("500 DB ");
          vm.requestFailPopup();
        } else if(e.response.status == 502) {
          console.log("502 Unknown error");
          vm.requestFailPopup();
        }
      });
  },
  beforeDestroy() {
    let vm = this;
    if (vm.deletePost) return;
    if (vm.isLikedBefore == vm.isLikedAfter) return; //    
    const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
    let task = "";
    if(vm.isLikedBefore) task = ""; //  
    else task = ""; //  

    console.log(task);
    const likeInfo = JSON.stringify({
      "like_id": 0,
      "nickname": UserInfo.nickname,
      "postType": 2,
      "postId": vm.requestPhoto.post_id,
      "task": task
    });
    herokuAPI.photoLike(likeInfo)
      .then(function (response) {
        if(response.status == 200) console.log(" " + task + " ");
      })
      .catch(function (e) {
        if(e.response.status == 500) {
          console.log("500  ");
          vm.likeFailPopup(task);
        } else if(e.response.status == 501) {
          console.log("501  ");
          vm.likeFailPopup(task);
        } else if(e.response.status == 502) {
          console.log("502 Unknown error");
          vm.likeFailPopup(task);
        }
      });
  },
  methods: {
    showDialog() { //  
      this.popupDialog = true;
    },
    hideDialog() { //  
      this.popupDialog = false;
    },
    deletePopup() {
      this.headerTitle = "   ";
      this.content1 = "?";
      this.btn1Title = "";
      this.btn2Title = "";
      this.btn2 = true;
      this.showDialog();
    },
    deleteFailPopup() {
      this.headerTitle = "    ";
      this.content1 = "  .";
      this.btn1Title = "";
      this.btn2 = false;
      this.showDialog();
    },
    requestFailPopup() {
      this.headerTitle = "  ";
      this.content1 = "  ";
      this.content2 = ".";
      this.btn1Title = "";
      this.btn2 = false;
      this.showDialog();
    },
    likeFailPopup(text) {
      this.headerTitle = " "+text+" ";
      this.content1 = " "+text+" .";
      this.btn1Title = "";
      this.btn2Title = "";
      this.btn2 = true;
      this.showDialog();
    },
    checkDialog() { //   
      //     
      this.deletePhoto();
      this.hideDialog();
    },
    deletePhoto() {
      let vm = this;
      vm.deletePost = true;
      const deleteTarget = {
        "post_id": vm.requestPhoto.post_id,
        "photo_link": "",
        "like_count": 0,
        "upload_time": "",
        "nickname": vm.requestPhoto.nickname
      }
      herokuAPI.photoDelete(deleteTarget) 
        .then(function (response) {
          if(response.status == 200) {
            router.push({name: 'photo'});
          }
        })
        .catch(function (e) {
          if(e.response.status == 500) {
            console.log("500 DB ");
            vm.deleteFailPopup();
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.deleteFailPopup();
          }
        });
    },

    showReportDialog() { //  
      this.reportDialog = true
    },
    hideReportDialog() { //  
      this.reportDialog = false
    },

    showMailCreate(){ //  
      this.createMailDialog = true;
    },
    hideMailCreate(){ //  
      this.createMailDialog = false;
    },

    likePhoto() { //    ,         
      this.isLikedAfter = !this.isLikedAfter;
      if(this.isLikedAfter) ++this.requestPhoto.like_count;
      else --this.requestPhoto.like_count;
    },
  }
}
</script>

<style>
.back{
  color: aqua;
}
.v-btn__content{
  display: flex;
  flex-direction: column;
}
.thumbs{
  color: rgb(248, 62, 62);
  font-size: 1.7em;
  font-weight: 600;
}
</style>