<template>
  <v-container>
    <v-row justify="center">
      <v-col class="col-xl-8 col-md-10">
        <v-card min-height="calc(100vh - 120px)" color="#f5efe6">

          <div class="recipe-top d-flex justify-space-between align-center pa-5">
            <span style="color:#7895B2; font-weight:900; font-size:1.3em;">  </span>
            <dropdown class="my-dropdown-toggle my-5 "
              :options="search_standard" 
              :selected= currentSearchOption
              v-on:updateOption="methodToRunOnSelect" 
              :placeholder="' '"
              :closeOnOutsideClick="true"
            ></dropdown>
          </div>

          <!--   -->
          <v-btn fab to="/photo/create" x-large color="primary" class="write-icon">
            <v-icon dark>mdi-pencil-outline</v-icon>
          </v-btn>

          <v-row justify="center">
            <v-col cols="8">
              <v-card height="400" v-for="item in photo" :key="item.post_id" class="my-10" @click="toLookup(item.post_id)">
                <v-row>
                  <v-col class="d-flex justify-space-between">
                    <div class="pl-5">
                      <v-card-title>{{item.nickname}}</v-card-title>
                      <v-card-subtitle>{{item.upload_time.split(/[T]/)[0]}}</v-card-subtitle>
                    </div>
                    <div>
                      <v-icon class="pa-6" large>
                        mdi-thumb-up-outline
                      </v-icon>
                      <span class="pr-6 like-count">{{item.like_count}}</span>
                    </div>
                  </v-col>
                </v-row>

                <div class="mx-6 mt-3">
                  <v-img
                    max-height="250"
                    contain
                    :src="'data:image/jpeg;base64,'+item.photo_link"
                  ></v-img>
                </div>
                    
              </v-card>

              <!--   -->
              <v-pagination 
                v-model="page"
                :length="pageLength"
                class="pb-10"
                @input="handlePage"
              >
              </v-pagination>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle=headerTitle
        :btn1Title=btn1Title
        :btn2=btn2
        @hide="hideDialog"
      >
        <template v-slot:body>
          <!--    -->
          <div>{{ content1 }}</div>
        </template>
      </popup-dialog>
    </v-dialog>

  </v-container>
</template>

<style>
.sort-base{
  font-size: 1.2em;
}
.like-count{
  font-size: 1.6em;
}
.write-icon{
  position: fixed;
  bottom: 10%;
  right: 5%;
  z-index: 9;
}

</style>

<script>
import herokuAPI from '@/api/heroku.js';
import router from '@/router/index.js';
import dropdown from 'vue-dropdowns';
import PopupDialog from '@/components/popup.vue';

export default{
  components: {
    'dropdown': dropdown,
    PopupDialog,
  },
  data() {
    return {
    //
      popupDialog: false,
      headerTitle: "",
      content1: "",
      btn1Title: "",
      btn2: false,

    // 
      photo: [],
      photoID: null,

    // ( ?)
      search_standard: [
          {name: ' '},
          {name: ' '}
      ],

      currentSearchOption : {
        name: " "
      },

      search_object: {
        name: " ",
      },
      page: 1,
      pageLength: null,
    };
  },
  mounted() {
    let vm = this;
    herokuAPI.photoList(1)
      .then(function(response) {
        console.log(" ", response);
        if(response.status == 200) {
          console.log(" ");
          vm.pageLength = response.data.total_page;
          console.log(vm.pageLength);
          for(let i = 0; response.data.photoList[i] != null; i++) {
            vm.photo.push(response.data.photoList[i]);
          }
          console.log(vm.photo);
        }
      })
      .catch(function (e) {
        if(e.response.status == 500) {
          console.log("500 DB ");
          vm.requestFailPopup();
        } else if(e.response.status == 502) {
          console.log("502 Unknown error");
          vm.requestFailPopup();
        }
      });
  },
  methods: {
  //  
    showDialog() { //  
      this.popupDialog = true;
    },
    hideDialog() { //  
      this.popupDialog = false;
    },
    requestFailPopup() { // 
      this.headerTitle = " ";
      this.content1 = "   .";
      this.showDialog();
    },
    sortRequestFailPopup() { //  
      this.headerTitle = " ";
      this.content1 = "   .";
      this.showDialog();
    },

  //      
    toLookup(photoID) {
      router.push({
        path: "/photo/lookup/"+photoID,
      })
    },

  //     
    sortPhotoList(page, order_by) {
      let vm = this;
      vm.photo = [];

      const sortInfo = JSON.stringify({
        "arrangeBy": order_by,
        "page": page
      });

      herokuAPI.photoSort(sortInfo)
        .then(function (response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log(" ");
            for(let i = 0; response.data.photoList[i] != null; i++) {
              vm.photo.push(response.data.photoList[i]);
            }
            console("  : ", vm.photo);
            window.scrollTo({top:0});
          }
        })
        .catch(function (e) {
          if(e.response.status == 500) {
            console.log("500 DB ");
            vm.sortRequestFailPopup();
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.sortRequestFailPopup();
          }
        });
    },

  //    
    methodToRunOnSelect(payload) {
      this.object = payload;
      if (this.object.name == " ") {
        console.log("  ");
        this.currentSearchOption.name = " ";
        this.page=1;

        this.sortPhotoList(this.page, this.currentSearchOption.name);
      } else if(this.object.name == " ") {
        console.log("  ");
        this.currentSearchOption.name = " ";
        this.page=1;
        this.sortPhotoList(this.page, this.currentSearchOption.name);
      }
    },

    handlePage(){
      let vm = this;

      if(this.currentSearchOption.name == " ") {
        this.sortPhotoList(vm.page, this.currentSearchOption.name);
      } else if(this.currentSearchOption.name == " ") {
        this.sortPhotoList(vm.page, this.currentSearchOption.name);
      }
    },
  }
}
</script>