<template>
  <v-card>
    <!--   -->
    <div class="d-flex justify-lg-space-between align-start pa-3">
      <!--   -->
      <v-card-title>
        {{ mailTitle }}
      </v-card-title>
      <!-- ,   -->
      <div class="v-flex">
        <div class="text-start">
           &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{ mailSender }} 
        </div>
        <div class="text-start">
           &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{ mailReceiver }}
        </div>
        <div class="text-start">
           &nbsp;&nbsp;&nbsp;&nbsp; {{ sendTime.split(/[T]/)[0] }}
        </div>    
      </div>
    </div>
    

    <v-card class="ma-4">
      <v-card-text class="text-left">
        <slot name="body">
          {{ mailContents }} 
        </slot>
      </v-card-text>
    </v-card>
    <!--   -->

    <!--   -->
    <v-card-actions class="justify-center mr-2 pb-4">
      <template>
        <v-btn
          color="#7895B2"
          small
          @click="deletePopup"
        >
          
        </v-btn>
      </template>
      <v-btn 
        color="#7895B2"
        small
        @click="$emit('hide')"
      >
        
      </v-btn>
    </v-card-actions>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle =titlePopup
        :btn1Title=titleBtn1
        :btn2Title=titleBtn2
        :btn2=btn2
        @hide="hideDialog"
        @submit="checkDialog"
      >
        <template v-slot:body>
          <!--    -->
          <div> {{ content }} </div>
        </template>
      </popup-dialog>
    </v-dialog>
    <!--   -->

  </v-card>
</template>

<script>
import PopupDialog from '@/components/popup.vue';
import herokuAPI from '@/api/heroku.js';

export default{
  name: "LookupMail",
  components: {
    PopupDialog
  },
  data() {
    return {
      popupDialog: false,
      titlePopup: "",
      titleBtn1: "",
      titleBtn2: "",
      btn2: false,
      content: ""
    };
  },
  mounted() {
    let vm = this;
    if(vm.mailTitle == null || vm.mailID == null ||vm.mailSender == null ||vm.mailReceiver == null ||vm.mailContents == null ||vm.mailID == null) {
      vm.openFailPopup();
    }
  },
  props: {
    mailTitle: {
      type: String,
    },
    mailID: {
      type: Number,
    },
    mailSender: {
      type: String,
    },
    mailReceiver: {
      type: String,
    },
    sendTime: {
      type: String,
    },
    mailContents: {
      type: String,
    }
  },
  methods: {
    showDialog(){ //  
      this.popupDialog = true
    },
    hideDialog(){ //  
      this.popupDialog = false
    },
    checkDialog(){ //   
      //     
      this.deleteMail();
      this.hideDialog();
    },
    openFailPopup(){
      this.titlePopup = "  ";
      this.content = "  .";
      this.titleBtn1 = "";
      this.btn2 = false;
      this.showDialog();
    },
    deleteFailPopup(){
      this.titlePopup = " ";
      this.content = " .";
      this.titleBtn1 = "";
      this.btn2 = false;
      this.showDialog();
    },
    deletePopup(){
      this.titlePopup = " ";
      this.content = " ?";
      this.titleBtn1 = "";
      this.titleBtn2 = "";
      this.btn2 = true;
      this.showDialog();
    },
    emitMethod(){
      this.$emit('update');
      this.$emit('hide');
    },
    deleteMail(){
      let vm = this;
      console.log("  ");
      const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
      const deleteTarget = JSON.stringify (
        {
          "mail_id" : this.mailID,
          "nickname" : UserInfo.nickname,
          "receiver" : "bye",
          "title" : "web test",
          "contents" : "web test",
          "send_time" : "",
          "sender_check" : 0,
          "receiver_check" : 0
        }
      );
      herokuAPI.mailDelete(deleteTarget) 
        .then(function (response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log("if  ", response.data);
            vm.emitMethod();
          }
        })
        .catch(function (e) {
          if(e.response.status == 404) {
            console.log("404 error");
            vm.deleteFailPopup();
          } else if(e.response.status == 500) {
            console.log("500 Unknown error");
            vm.deleteFailPopup();
          }
        });
    },
  }
};
</script>