                        <div v-if="item.expireDate == null">
                          <!-- 7    -->
                          <div class="d-flex justify-space-between">
                            <span class="ml-3 my-2" style="font-size:1.8em">{{item.name}}</span>
                            <span class="mr-3 my-2" style="font-size:1.8em">{{item.amount}}{{item.unit}}</span>
                          </div>

                          <!--  -->
                          <v-divider />

                          <!--  -->
                          <div class="d-flex justify-space-between align-center">
                            <span class="ml-3" style="font-size:1.2em">{{item.expiry_date}}</span>
                            <div class="mr-3">
                              <v-btn small color="#AEBDCA" class="my-2" @click="beforeEdit(item)"></v-btn>
                              <v-btn small color="#AEBDCA" class="ml-3 my-2" @click="deletePopup(item.id)"></v-btn>
                            </div>
                          </div>
                        </div>
                        
                      </div>
                    </v-card-text>
                    
                  </v-card>
                </v-col>
              </v-row>  
            </v-layout>
          </v-card>
        </v-col>
      </v-row>
    </v-layout>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle=headerTitle
        :btn1Title=btn1Title
        :btn2Title=btn2Title
        :btn2=btn2
