// Pagination

<script>
import herokuAPI from '@/api/heroku.js';
import PopupDialog from '@/components/popup.vue';
import AddIngredientDialog from '@/components/addIngredient.vue';
import EditIngredientDialog from '@/components/editIngredient.vue';

export default{
  components: {
    PopupDialog,
    AddIngredientDialog,
    EditIngredientDialog
  },
  data(){
    return{
      snackbar: false,
      snackbarContents: "",

      popupDialog: false,
      headerTitle: "",
      content1: "",
      btn1Title: "",
      btn2Title: "",
      btn2: false,
      id: -1,
      name: "",
      amount: -1,
      unit: "",
      addIngredientDialog: false,
      editIngredientDialog: false,
      refrigerators: [],
      isEmpty: false,

      //   
      todayDate : null,
    }
  },
  mounted() {
    //  
    const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
    let vm = this;

    //    
    let now = new Date();
    console.log(now);

    herokuAPI.refrigeratorLookup(UserInfo.nickname)
      .then(function(response) {
        console.log(" ", response);
        if(response.status == 200) {
            console.log(" ");
            for(let i = 0; response.data[i] != null; i++) {
              if(response.data[i].expiry_date != null){
                var temp = new Date(response.data[i].expiry_date);
                response.data[i].expireDate = Math.ceil((temp-now)/(1000*60*60*24));
              } else {
                response.data[i].expireDate = null;
              }
              
              vm.refrigerators.push(response.data[i]);
            }
          }
      })
      .catch(function (e) {
        if(e.response.status == 401) {
          console.log(" ");
          vm.requestFailPopup();
        } else if(e.response.status == 404) {
          console.log("404  ");
          vm.isEmpty = true;
        } else if(e.response.status == 500) {
          console.log("500 Unknown error");
          vm.requestFailPopup();
        }
      });
  },
  methods: {
  //  
    showDialog() { //  
      this.popupDialog = true;
    },
    hideDialog() { //  
      this.popupDialog = false;
    },
    deletePopup(click_id) {
      this.headerTitle = " ";
      this.content1 = "  ?";
      this.btn1Title = "";
      this.btn2Title = "";
      this.btn2 = true;
      this.id = click_id;
      this.showDialog();
    },
    deleteFailPopup() {
      this.headerTitle = " ";
      this.content1 = "  .";
      this.btn1Title = "";
      this.btn2 = false;
      this.showDialog();
    },
    requestFailPopup() {
      this.headerTitle = " ";
      this.content1 = "   .";
      this.btn1Title = "";
      this.btn2 = false;
      this.showDialog();
    },
    addRequestFailPopup() {
      this.headerTitle = " ";
      this.content1 = "   .";
      this.btn1Title = "";
      this.btn2 = false;
      this.showDialog();
    },
    deleteIngre() {
      let vm = this;
      const deleteTarget = JSON.stringify ({ "id": this.id });
      herokuAPI.refrigeratorDelete(deleteTarget)
        .then(function(response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log(" ");
            vm.$router.go();
          }
        })
        .catch(function (e) {
        if(e.response.status == 406) {
          console.log("406  ");
          vm.deleteFailPopup();
        } else if(e.response.status == 500) {
          console.log("500 Unknown error");
          vm.deleteFailPopup();
        }
      });
    },

  //   
    showAddIngredientDialog() {
      this.addIngredientDialog = true;
    },
    hideAddIngredientDialog() {
      this.addIngredientDialog = false;
    },
    add(ingre) {
      let vm = this;
      var index = this.refrigerators.findIndex(e => e.name === ingre.name);
      if(index != -1) {
        vm.snackbarContents = "  ."
        vm.snackbar = true;
        return;
      }
      const AddIngre = JSON.stringify (ingre);
        console.log(AddIngre);
        herokuAPI.refrigeratorAdd(AddIngre)
          .then(function (response) {
            console.log("response", response);
            if(response.status == 200) {
              console.log(" ", response.data);
              vm.$router.go();
            }
          })
          .catch(function (e) {
            if(e.response.status == 400) {
              console.log("400 error");
              vm.addRequestFailPopup();
            } else if(e.response.status == 500) {
              console.log("500 Unknown error");
              vm.addRequestFailPopup();
            }
          });
    },
  //   
    showEditIngredientDialog() {
      this.editIngredientDialog = true;
    },
    hideEditIngredientDialog() {
      this.editIngredientDialog = false;
    },
    beforeEdit(object) {
      this.id = object.id;
      this.name = object.name;
      this.amount = object.amount;
      this.unit = object.unit;
      this.showEditIngredientDialog();
    },
    update() {
      this.$router.go();
    },
  }
}
</script>

