<template>
  <v-container>
    <v-layout>
      <v-row justify="center">
        <v-col cols="8">
          <v-card color="#f5efe6" height="800">
            <v-card color="#f5efe6" height="200" flat>
              
            </v-card>
            <v-layout>
              <v-row justify="center">
                <v-col class="col-xl-4 col-md-6">
                  <v-card color="#fefefe" height="400" rounded="xl">
                    <v-card-title id="title" class="pa-10 justify-center fontsize"> </v-card-title>

                    <v-col>
                      <v-form ref="form" v-model="valid" lazy-validation>
                        <v-text-field 
                          v-model="user.pw" 
                          filled 
                          label=" " 
                          :rules="pw_rule"
                          type="password"
                          placeholder="  ."
                          required
                        ></v-text-field>
                        <v-text-field 
                          v-model="user.pwcheck" 
                          filled 
                          label=" " 
                          :rules="pwch_rule"
                          type="password"
                          placeholder=" ."
                          required
                        ></v-text-field>
                      </v-form>
                    </v-col>

                    
                    <v-col>
                      <v-row justify="center">
                        <v-card-actions>
                          <v-btn outlined width="100" to="/mypage"></v-btn>
                        </v-card-actions>
                        <v-card-actions>
                          <v-btn outlined width="100" @click="PWchange()"></v-btn>
                        </v-card-actions>
                      </v-row>
                      
                    </v-col>
                  </v-card>
                </v-col>
              </v-row>
            </v-layout>
            
          </v-card>
        </v-col>
      </v-row>
    </v-layout>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle=headerTitle
        btn1Title=""
        :btn2=false
        @hide="hideDialog"
      >
        <template v-slot:body>
          <!--    -->
          <div> {{ content1 }} </div>
        </template>
      </popup-dialog>
    </v-dialog>

    <v-snackbar v-model="snackbar" timeout="3000">
      {{ snackbarContents }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>

  </v-container>
</template>

<style>
  #title{
    font-weight: 700;
    font-size: 2em;
  }

</style>

<script>
import herokuAPI from '@/api/heroku.js';
import router from '@/router/index.js';
import PopupDialog from '@/components/popup.vue';

export default{
  components: {
    PopupDialog
  },
  data() {
    return {
      valid: true,
    // 
      popupDialog: false,
      headerTitle: "",
      content1: "",

      snackbar: false,
      snackbarContents: "",

    // 
      user: {
        email: '',
        id: '',
        nickname: '',
        pw: '',
        pwcheck: '',
        al: ''
      },
      nickname: '',

    //  
      pw_rule: [
        v => !!v || '  .',
        v => !(v && v.length < 6) || ' 6  .',
        v => /^[a-z0-9!?@<>]*$/.test(v) || '  , , (!, ?, @, <, >)  .',
        v => /^.*[a-z]+.*$/.test(v) || '    .',
        v => /^.*[0-9]+.*$/.test(v) || '   .',
        v => /^.*[!?@<>]+.*$/.test(v) || ' (!, ?, @, <, >)  .',
      ],
      pwch_rule: [
        v => !!v || '  .',
        v => v === this.user.pw || '  .',
      ],
    };
  },
  created() {
    const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
    this.nickname = UserInfo.nickname
    this.user.id = UserInfo.uid
    this.user.email = UserInfo.email
    this.user.al = UserInfo.auto_login
  },
  methods: {
    showDialog(){ //  
      this.popupDialog = true
    },
    hideDialog(){ //  
      this.popupDialog = false
    },
    PWchange() {
      const vm = this;
      const validate = this.$refs.form.validate();
      if(!validate) {
        vm.headerTitle = " ";
        vm.content1 = "  .";
        vm.showDialog();
        return;
      }
      let hashedPWD = this.hashing(vm.user.pw);

      const userInfo = JSON.stringify({
        "nickname": this.nickname,
        "uid": this.user.id,
        "password": hashedPWD,
        "email": this.user.email,
        "auto_login": this.user.al,
      });
      console.log(userInfo);
      herokuAPI.changePW(userInfo)
        .then(function (response) {
          console.log("pwChange", response)
          if(response.status == 200) {
            console.log(" ")
            localStorage.setItem("UserInfo", userInfo);
            router.push({name: 'mypage'});
          }
        }) 
        .catch(function (e) {
          if(e.response.status == 500) {
            console.log("500   ");
            vm.snackbarContents = "DB     ."
            vm.snackbar = true;
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.snackbarContents = "       ."
            vm.snackbar = true;
          }
        });
      
    },
    hashing(content){
      var result = "";
      for(let i = 0; i < content.length; i++){
        result += String.fromCharCode(Number(content.charCodeAt(i))+1);
      }
      return result;
    },
    rhashing(content){
      var result = "";
      for(let i = 0; i < content.length; i++){
        result += String.fromCharCode(Number(content.charCodeAt(i))-1);
      }
      return result;
    }
  }
}
</script>