<template>
  <v-card>
    <!--    -->
    <v-card-text>

      <div style="color:#7895B2; font-size: 2.1em;" class="py-6">
          
      </div>

      <div style="color:#7895B2; font-size: 1.3em;">
         
      </div>

      <div class="searchbox mb-6">
        <div class="header">
          <v-text-field
            class="mx-6"
            v-model="name"
            disabled
          ></v-text-field>
        </div>
      </div>
      
      <div style="color:#7895B2; font-size: 1.3em;">
        
      </div>

      <v-text-field
        placeholder=" "
        hide-details="auto"
        id="inputText"
        class="mx-6 mb-8"
        v-model="amount"
      ></v-text-field>

      <div style="color:#7895B2; font-size: 1.3em;">
        
      </div>

      <v-text-field
        placeholder=" "
        id="inputUnit"
        @click="units_filter"
        class="mx-6 mb-0"
        v-model="unit"
      ></v-text-field>

      <div class="units-container mx-6">
        <div v-for="i in units" :key="i.name">
          <span ref="unitsName" class="unitsName visible" @click="clickUnit">{{i.name}}</span>
        </div>
        <div class="mb-13"></div>
      </div>

      <div style="color:#7895B2; font-size: 1.3em;">
        <br/>  
        <span style="font-size: 0.7em;">    .</span>
      </div>

      <v-date-picker
        v-model="expiry_date" 
        :landscape="true" 
        color="#7895B2"
        class=""
      >
      </v-date-picker>      

      <div class="d-flex justify-center">
         <v-checkbox label="  " v-model="is_expiry_not_exist"></v-checkbox>
      </div>
     
      
    </v-card-text>

    <!--   -->
    <v-card-actions class="justify-center">
      <v-btn
        color="#7895B2"
        x-large
        @click="editIngre()"
      >
        
      </v-btn>
    </v-card-actions>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle=headerTitle
        btn1Title=""
        :btn2="false"
        @hide="hideDialog"
      >
        <template v-slot:body>
          <!--    -->
          <div>{{ content1 }}</div>
        </template>
      </popup-dialog>
    </v-dialog>

  </v-card>
</template>

<style>

.searchbox{
  overflow: hidden;
}

.ingredient-container{
  max-height:100px;
  overflow: scroll;
}

.units-container{
  overflow: auto;
}

.ingreName{
  display: none;
  cursor: pointer;
}

.unitsName{
  display: none;
  cursor: pointer;
}

.visible{
  display: flex;
}

</style>

<script>
import herokuAPI from '@/api/heroku.js';
import PopupDialog from '@/components/popup.vue';

export default{
  name: "editIngredient",
  components: {
    PopupDialog,
  },
  data() {
    return {
      popupDialog: false,
      headerTitle: "",
      content1: "",

      name: "",
      amount: "",
      unit: "",
      expiry_date: null,
      nickname: "",

      //  
      units : [
        {name : "g"},
        {name : "kg"},
        {name : "l"},
        {name : "ml"},
        {name : "T"},
      ],

      //    
      unitLabel : " ",
      
      //     
      is_expiry_not_exist : false,
    };
  },
  props: {
    isRecipe: {
      type: Boolean,
      default: false,
    },
    idP: {
      type: Number
    },
    nameP: {
      type: String
    },
    amountP: {
      type: Number
    },
    unitP: {
      type: String
    },
  },
  watch: {
    nameP: function() {
      let vm = this;
      vm.name = vm.nameP;
      vm.amount = vm.amountP;
      vm.unit = vm.unitP;
    }
  },
  mounted(){
    let vm = this;
    const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
    vm.nickname = UserInfo.nickname;

    vm.name = vm.nameP;
    vm.amount = vm.amountP;
    vm.unit = vm.unitP;

    //   
    for (var i=0;i<vm.units.length;i++){
      vm.$refs.unitsName[i].classList.remove('visible');
    }
  },
  methods: {
  //  
    showDialog() { //  
      this.popupDialog = true;
    },
    hideDialog() { //  
      this.popupDialog = false;
    },
    invalidPopup() {
      this.headerTitle = " ";
      this.content1 = "   .";
      this.showDialog();
    },
    editFailPopup() {
      this.headerTitle = " ";
      this.content1 = "  .";
      this.showDialog();
    },
    editIngre() {
      let vm = this;
      if(vm.name == "") {
        vm.invalidPopup();
        return;
      }
      if(vm.amount == "") {
        vm.invalidPopup();
        return;
      }
      if(vm.unit == "") {
        vm.invalidPopup();
        return;
      }
      const temp = {
        "id": vm.idP,
        "amount": Number(vm.amount),
        "expiry_date": this.is_expiry_not_exist ? null : vm.expiry_date, //  
        "name" : vm.name,
        "unit" : vm.unit,
      };
      if(vm.isRecipe == true) {
        vm.$emit('hide');
        vm.$emit('edit', temp);
      } else {
        const Ingre = JSON.stringify (temp);
        console.log(Ingre);
        herokuAPI.refrigeratorEdit(Ingre)
          .then(function (response) {
            console.log("response", response);
            if(response.status == 200) {
              console.log(" ", response.data);
              vm.$emit('hide');
              vm.$emit('update');
            }
          })
          .catch(function (e) {
            if(e.response.status == 406) {
              console.log("406  error");
              vm.editFailPopup();
            } else if(e.response.status == 500) {
              console.log("500 Unknown error");
              vm.editFailPopup();
            }
          });
      }
    },
    //  
    units_filter(){
      for (var i=0;i<this.units.length;i++){
        this.$refs.unitsName[i].classList.add('visible');
      }

    },
    //    
    clickUnit(payload){
      let vm = this;
      document.getElementById("inputUnit").value = " ";
      document.getElementById("inputUnit").value = payload.srcElement.innerText;
      vm.unit = payload.srcElement.innerText;
      for (var i=0;i<this.units.length;i++){
        this.$refs.unitsName[i].classList.remove('visible');
      }
    }
  }
}
</script>