<script>
import herokuAPI from '@/api/heroku.js';
import dropdown from 'vue-dropdowns';
import router from '@/router/index.js';
import PopupDialog from '@/components/popup.vue';
import ReportDialog from '@/components/report.vue';
import CreateMailDialog from '@/components/createMail.vue';

export default{
  components: {
    PopupDialog,
    ReportDialog,
    CreateMailDialog,
    'dropdown': dropdown,
  },
  data () {
    return {
    // 
      popupDialog: false,
      reportDialog: false,
      createMailDialog: false,
      headerTitle: "",
      content1: "",
      content2: "",
      btn1Title: "",
      btn2Title: "",
      btn2: false,
      reportType: 0,
      reportID: 0,

      requestRecipe: [],
      UnExistIngre: [],
      changeComment: "",
      changeComment_rule: [
        v => !!v || '  .',
      ],
      newComment: "",
      newComment_rule: [
        v => !!v || '  .',
      ],
      isLikedBefore: null,
      isLikedAfter: null,
      isMine: null,
      deletePost: false,
      isRecipeDelete: true,
      deleteCommentID: -1,
      editCommentID: -1,

      recippeType: [
        { name: ' '},
        { name: ' '},
        { name: ' '}
      ],
      recippeTypeObject: {
        name: '',
      },
      hotLevel: [
        { name: '1'},
        { name: '2'},
        { name: '3'},
        { name: '4'},
        { name: '5'}
      ],
      hotLevelObject: {
        name: '3',
      },
      hotLevel:4,
      canDecrease:false,
      userNN: ""
    }
  },
  mounted() {
    let pid = this.$route.params.id;
    console.log("post_id", pid);
    if(pid == null) {
      console.log("ERROR");
    }
    const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
    let vm = this;
    vm.userNN = UserInfo.nickname;
    
  //   
    herokuAPI.recipeLookup(pid, vm.userNN)
      .then(function(response) {
        console.log("  ", response);
        if(response.status == 200) {
            console.log(" ");
            vm.requestRecipe = response.data.recipeInfo;
            vm.isLikedBefore = response.data.likeInfo;
            vm.isLikedAfter = vm.isLikedBefore;
            vm.hotLevel = vm.requestRecipe['degree_of_spicy']
            if(vm.requestRecipe.nickname == vm.userNN) {
              vm.isMine = true;
            } else {
              vm.isMine = false;
            }
          }
      })
      .catch(function (e) {
        if(e.response.status == 500) {
          console.log("500 DB ");
          vm.requestFailPopup();
        } else if(e.response.status == 502) {
          console.log("502 Unknown error");
          vm.requestFailPopup();
        }
      });

  //   
    herokuAPI.unExistIngredients(vm.userNN, pid)
      .then(function (response) {
        console.log("test",response.data);
        console.log("test",response.data.length);
        if(response.status == 200) {
          console.log("   ")
          for(let i = 0; i<response.data.length; i++) {
            vm.UnExistIngre.push(response.data[i].id);
          }
          if(response.data.length == 0) vm.canDecrease = true;
          else vm.canDecrease = false;
        }
      })
      .catch(function (e) {
        if(e.response.status == 500) {
          console.log("500 DB ");
          vm.ingreFailPopup();
        } else if(e.response.status == 502) {
          console.log("502 Unknown error");
          vm.ingreFailPopup();
        }
      });
  },
  beforeDestroy() {
    let vm = this;
    if (vm.deletePost) return;
    if (vm.isLikedBefore == vm.isLikedAfter) return; //    
    let task = "";
    if(vm.isLikedBefore) task = ""; //  
    else task = ""; //  

    console.log(task);
    const likeInfo = JSON.stringify({
      "like_id": 0,
      "nickname": vm.userNN,
      "postType": 1,
      "postId": vm.requestRecipe.post_id,
      "task": task
    });
    herokuAPI.recipeLike(likeInfo)
      .then(function (response) {
        if(response.status == 200) console.log(" " + task + " ");
      })
      .catch(function (e) {
        if(e.response.status == 500) {
          console.log("500  ");
          vm.likeFailPopup(task);
        } else if(e.response.status == 501) {
          console.log("501  ");
          vm.likeFailPopup(task);
        } else if(e.response.status == 502) {
          console.log("502 Unknown error");
          vm.likeFailPopup(task);
        }
      });
  },
  methods: {
    showDialog() { //  
      this.popupDialog = true;
    },
    hideDialog() { //  
      this.popupDialog = false;
    },
    deletePopup() {
      this.headerTitle = "  ";
      this.content1 = "?";
      this.btn1Title = "";
      this.btn2Title = "";
      this.btn2 = true;
      this.isRecipeDelete = true;
      this.showDialog();
    },
    commentDeletePopup(id) {
      this.deleteCommentID = id;
      this.headerTitle = " ";
      this.content1 = "?";
      this.btn1Title = "";
      this.btn2Title = "";
      this.btn2 = true;
      this.isRecipeDelete = false;
      this.showDialog();
    },
    deleteFailPopup() {
      this.headerTitle = "   ";
      this.content1 = "  .";
      this.btn1Title = "";
      this.btn2 = false;
      this.showDialog();
    },
    requestFailPopup() {
      this.headerTitle = "  ";
      this.content1 = "  ";
      this.content2 = ".";
      this.btn1Title = "";
      this.btn2 = false;
      this.showDialog();
    },
    ingreFailPopup() {
      this.headerTitle = "    ";
      this.content1 = "   ";
      this.content2 = "";
      this.btn1Title = "";
      this.btn2 = false;
      this.showDialog();
    },
    likeFailPopup(text) {
      this.headerTitle = " "+text+" ";
      this.content1 = " "+text+" .";
      this.btn1Title = "";
      this.btn2Title = "";
      this.btn2 = true;
      this.showDialog();
    },

    checkDialog() { //   
      //     
      if(this.isRecipeDelete == true) this.deleteRecipe();
      else this.deleteComment();
      this.hideDialog();
    },
    deleteRecipe() { //  
      let vm = this;
      vm.deletePost = true;
      const deleteTarget = JSON.stringify({ requestRecipe });
      herokuAPI.recipeDelete(deleteTarget)
        .then(function (response){
          if(response.status == 200) {
            console.log(" ");
            router.push({name: 'recipe'});
          }
        })
        .catch(function (e) {
          if(e.response.status == 500) {
            console.log("500 DB ");
            vm.deleteFailPopup();
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.deleteFailPopup();
          }
        });
    },
    deleteComment() {
      let vm = this;
      const deleteInfo = JSON.stringify({
        "comment_id": vm.deleteCommentID,
        "nickname": vm.userNN
      });
      herokuAPI.commentDelete(deleteInfo)
        .then(function (response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log("  ");
            vm.$router.go();
          }
        })
    },
    
    showReportDialog() { //   
      this.reportDialog = true
    },
    hideReportDialog() { //   
      this.reportDialog = false
    },
    reportRecipe() { //  
      let vm = this;
      vm.reportType = 1;
      vm.reportID = vm.requestRecipe.post_id;
      vm.showReportDialog();
    },
    reportComment(comment_id) { //  
      let vm = this;
      vm.reportType = -1;
      vm.reportID = comment_id;
      vm.showReportDialog();
    },

    showMailCreate(){ //    
      this.createMailDialog = true;
    },
    hideMailCreate(){ //    
      this.createMailDialog = false;
    },

    likeRecipe() { //    ,         
      this.isLikedAfter = !this.isLikedAfter;
      if(this.isLikedAfter) ++this.requestRecipe.like_count;
      else --this.requestRecipe.like_count;
    },

    unExistIngredients() {
    //   
      herokuAPI.unExistIngredients(vm.userNN, pid)
        .then(function (response) {
          console.log("test",response.data);
          console.log("test",response.data.length);
          if(response.status == 200) {
            console.log("   ")
            for(let i = 0; i<response.data.length; i++) {
              vm.UnExistIngre.push(response.data[i].id);
            }
            if(response.data.length == 0) vm.canDecrease = true;
            else vm.canDecrease = false;
          }
        })
        .catch(function (e) {
          if(e.response.status == 500) {
            console.log("500 DB ");
            vm.ingreFailPopup();
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.ingreFailPopup();
          }
        });
    },

    remainAmmounts() {
      let vm = this;
      herokuAPI.decreaseAmount(vm.userNN, vm.requestRecipe.post_id)
        .then(function (response) {
          if(response.status == 200) {
            console.log("   ");
            vm.unExistIngredients();
          }
        })
    },
    methodToRunOnSelect(payload) {
      this.object = payload;
    },
    addComment() {
      const validate = this.$refs.form.validate();
      if(!validate) return;
      console.log(" ");
      let vm = this;
      const NewComment = JSON.stringify({
        "comment_id" : 0,
	      "comments" : vm.newComment,
	      "comment_time" : 0,
	      "nickname" : vm.userNN,
	      "post_id" : vm.requestRecipe.post_id
      });
      herokuAPI.commentAdd(NewComment)
        .then(function (response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log("  ");
            vm.$router.go();
          }
        })
    },
    toEditComment(object) {
      this.editCommentID = object.comment_id;
      this.changeComment = object.comments;
    },
    editComment() {
      let vm = this;
      const changeComment = JSON.stringify({
        "comment_id" : vm.editCommentID,
	      "comments" : vm.changeComment,
	      "comment_time" : 0,
	      "nickname" : vm.userNN,
	      "post_id" : vm.requestRecipe.post_id
      });
      herokuAPI.commentEdit(changeComment)
        .then(function (response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log("  ");
            router.go();
          }
        })
    },

  //       .
    menuButtonOnClickMethod(){
      let menuBtn = document.querySelector(".menu-container");
      let mailBtn = document.querySelector(".mail-btn");
      let reportBtn = document.querySelector(".report-btn");
      let deleteBtn = document.querySelector(".delete-btn");
      let editBtn = document.querySelector(".edit-btn");

      if(menuBtn.classList.contains('visible')) {
        console.log("visible ");
        menuBtn.classList.remove("visible");
        deleteBtn.classList.remove("visible");
        mailBtn.classList.remove("visible");
        reportBtn.classList.remove("visible");
        editBtn.classList.remove("visible");
      } else {
        if(this.isMine) {
          deleteBtn.classList.add("visible");
          editBtn.classList.add("visible");
        }else {
          mailBtn.classList.add("visible");
          reportBtn.classList.add("visible");
        }
        console.log("visible ");
        menuBtn.classList.add("visible");
      }
    },
    //      .
    mailButtonOnClickMethod(){
      let menuBtn = document.querySelector(".menu-container");
      console.log("  !");
      menuBtn.classList.remove("visible");
      this.showMailCreate();
    },
    //  
    reportButtonOnClickMethod(){
      let menuBtn = document.querySelector(".menu-container");
      console.log("  !");
      menuBtn.classList.remove("visible");
      this.reportRecipe();
    },
    //  
    deleteButtonOnClickMethod(){
      let menuBtn = document.querySelector(".menu-container");
      console.log("  !");
      menuBtn.classList.remove("visible");
      this.deletePopup();
    },
    editButtonOnClickMethod(){
      let menuBtn = document.querySelector(".menu-container");
      console.log("  !");
      menuBtn.classList.remove("visible");
      router.push({
        path: "/recipe/edit/"+this.$route.params.id,
      })
    }
  }
}
</script>