<template>
  <v-container >
    <v-form ref="form" v-model="valid" lazy-validation>
      <v-row>
        <v-col cols="4" offset="4">
          <v-card-title style="justify-content: center"> </v-card-title>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="6" offset="3">
          <v-divider></v-divider>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="4" offset="4">
          <v-text-field 
            v-model="info.id" 
            label="id"
            :rules="id_rule"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="4" offset="4">
          <v-text-field 
            v-model="info.nick" 
            label="nickname"
            :rules="nn_rule"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="4" offset="4">
          <v-text-field 
            v-model="info.pw" 
            label="password"
            type="password"
            :rules="pw_rule"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="4" offset="4">
          <v-text-field 
            v-model="info.pwcheck" 
            label="check password"
            type="password"
            :rules="pwch_rule"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="auto">
          <v-btn to="/login"></v-btn>
        </v-col>
        <v-col cols="2">
          <v-btn @click="signup" style="width: 100%"></v-btn>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="6" offset="3">
          <v-divider></v-divider>
        </v-col>
      </v-row>
    </v-form>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle=title
        btn1Title=""
        :btn2=false
        @hide="hideDialog"
        @submit="checkDialog"
      >
        <template v-slot:body>
          <!--    -->
          <div>{{text1}}<br/>{{text2}}</div>
        </template>
      </popup-dialog>
    </v-dialog>

    <v-snackbar v-model="snackbar" timeout="3000">
      {{ snackbarContents }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>

  </v-container>
</template>

<script>
import herokuAPI from '@/api/heroku.js';
import router from '@/router/index.js';
import PopupDialog from '@/components/popup.vue';

export default{
  components: {
    PopupDialog
  },
  data() {
    return {
      valid: true,
      popupDialog: false,
      title: "",
      text1: "",
      text2: "",

      snackbar: false,
      snackbarContents: "",

      info: {
        email: null,
        id: null,
        nick: null,
        pw: null,
        pwcheck: null,
      },

    //  
      id_rule: [
        v => !!v || ' .',
        v => !(v && v.length < 6) || ' 6  .',
      ],
      nn_rule: [
        v => !!v || ' .',
      ],
      pw_rule: [
        v => !!v || '  .',
        v => !(v && v.length < 6) || ' 6  .',
        v => /^[a-z0-9!?@<>]*$/.test(v) || '  , , (!, ?, @, <, >)  .',
        v => /^.*[a-z]+.*$/.test(v) || '    .',
        v => /^.*[0-9]+.*$/.test(v) || '   .',
        v => /^.*[!?@<>]+.*$/.test(v) || ' (!, ?, @, <, >)  .',
      ],
      pwch_rule: [
        v => !!v || '  .',
        v => v === this.info.pw || '  .',
      ],
    }
  },
  created() {
    this.info.email = localStorage.getItem("email");
    console.log("  ", this.info.email);
    localStorage.removeItem("email");
  },
  methods: {
    showDialog(){ //  
      this.popupDialog = true
    },
    hideDialog(){ //  
      this.popupDialog = false
    },
    checkDialog(){ //   
      //     
      this.hideDialog();
    },
    signup() {
      let vm = this;
      const validate = this.$refs.form.validate();
      if(!validate) {
        vm.title = " ";
        vm.text1 = "  ."
        vm.showDialog();
        return;
      }

      let hashedPWD = this.hashing(vm.info.pw);

      const signupInfo = JSON.stringify({
        "nickname": this.info.nick,
        "uid": this.info.id,
        "password": hashedPWD,
        "email": this.info.email,
        "auto_login": false,
      });
      console.log(signupInfo);
      herokuAPI.signup(signupInfo)
        .then(function (response) {
          console.log(response)
          if(response.status == 200) {
            console.log(" ")
            router.push({name: 'login'});
          }
        }) 
        .catch(function (e) {
          if(e.response.status == 400) {
            console.log("400 error");
            vm.snackbarContents = "  ."
            vm.snackbar = true;
          } else if(e.response.status == 401) {
            console.log("401 error");
            vm.snackbarContents = "  ."
            vm.snackbar = true;
          } else if(e.response.status == 402) {
            console.log("402 error");
            vm.snackbarContents = " ,  ."
            vm.snackbar = true;
          } else if(e.response.status == 500) {
            console.log("500 error");
            vm.title = " ";
            vm.text1 = "  ."
            vm.text2 = " ."
            vm.showDialog();
          } else if(e.response.status == 502) {
            console.log("502 error");
            vm.title = " ";
            vm.text1 = "  ."
            vm.text2 = " ."
            vm.showDialog();
          }
        });
    },
    hashing(content){
      var result = "";
      for(let i = 0; i < content.length; i++){
        result += String.fromCharCode(Number(content.charCodeAt(i))+1);
      }
      return result;
    }
  }
}
</script>