<template>
  <v-container>
    <v-layout>
      <!--    -->
      <v-row justify="center">
        <v-col cols="8"> 
          <v-card color="#f5efe6" height="800">
            <v-card color='#f5efe6' height="100" flat></v-card>
            <!--    -->
            <v-row justify="center">
              <v-col cols="md-6 xl-4" align-content="center">
                <v-card color='#fefefe' height="600" rounded="xl">
          
                  <v-card-title class="justify-center"> </v-card-title>
                  <v-card-title class="pl-15"> </v-card-title>

                  <v-card-title class="justify-center pb-15">{{ nickname }}</v-card-title>
                  <v-card-title class="pt-15 pl-15"> </v-card-title>
                  
                  <v-col offset="2" cols="8">
                    <v-form ref="form" v-model="valid" lazy-validation>
                      <v-text-field 
                        filled 
                        v-model="user.nickname" 
                        label=" " 
                        :rules="nn_rule" 
                        placeholder="  ." 
                        class="pb-10"
                      ></v-text-field>
                    </v-form>
                  </v-col>

                  <v-row justify="center">
                    
                    <v-card-actions>
                      <v-btn outlined width="120" to="/mypage"></v-btn>
                    </v-card-actions>

                    <v-card-actions>
                      <v-btn outlined width="120" @click="NNchange()"></v-btn>
                    </v-card-actions>
                  
                  </v-row>

                </v-card>
              </v-col>
            </v-row>

          </v-card>
        </v-col>
      </v-row>
    </v-layout>

    <!--   -->
    <v-dialog
      max-width="300"
      v-model="popupDialog"
    >
      <popup-dialog
        :headerTitle=headerTitle
        btn1Title=""
        :btn2=false
        @hide="hideDialog"
      >
        <template v-slot:body>
          <!--    -->
          <div> {{ content1 }} </div>
          <div v-if="content2 != ''"> {{ content2 }} </div>
        </template>
      </popup-dialog>
    </v-dialog>

  </v-container>
</template>

<script>
import herokuAPI from '@/api/heroku.js';
import router from '@/router/index.js';
import PopupDialog from '@/components/popup.vue';

export default{
  components: {
    PopupDialog
  },
  data() {
    return {
      valid: true,

      popupDialog: false,
      headerTitle: "",
      content1: "",
      content2: "",
      
      user: {
        email: '',
        id: '',
        nickname: '',
        pw: '',
        pwcheck: '',
        al: ''
      },
      nickname: '',
      
    //  
      nn_rule: [
        v => !!v || '  .',
      ],
    };
  },
  created() {
    const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
    this.nickname = UserInfo.nickname
    this.user.id = UserInfo.uid
    this.user.pw = UserInfo.password
    this.user.email = UserInfo.email
    this.user.al = UserInfo.auto_login
  },
  methods: {
    showDialog(){ //  
      this.popupDialog = true
    },
    hideDialog(){ //  
      this.popupDialog = false
    },
    changeFailPopup() {
      this.headerTitle = " ";
      this.content1 = "  .";
      this.content2 = " .";
      this.showDialog();
    },
    sameNickPopup() {
      this.headerTitle = " ";
      this.content1 = " .";
      this.content2 = "";
      this.showDialog();
    },
    NNchange() {
      let vm = this;
      const validate = this.$refs.form.validate();
      if(!validate) {
        vm.headerTitle = "";
        vm.content1 = "  .";
        vm.showDialog();
        return;
      }
      const userInfo = JSON.stringify({
        "nickname": this.user.nickname,
        "uid": this.user.id,
        "password": this.user.pw,
        "email": this.user.email,
        "auto_login": this.user.al,
      });
      herokuAPI.changeNN(userInfo)
        .then(function (response) {
          console.log("nnChange", response)
          if(response.status == 200) {
            console.log(" ")
            localStorage.setItem("UserInfo", userInfo);
            router.push({name: 'mypage'});
          }
        }) 
        .catch(function (e) {
          if(e.response.status == 400) {
            console.log("400   error");
            vm.sameNickPopup();
          } else if(e.response.status == 500) {
            console.log("500    or DB");
            vm.changeFailPopup();
          } else if(e.response.status == 502) {
            console.log("502 Unknown error");
            vm.changeFailPopup();
          }
        });
    }
  }
}
</script>